body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.visible {
  display: block;
}

.invisible {
  display: none;
}

.answer,
.button {
  display: none;
}

.disabled {
  pointer-events: none;
}

.correct {
  color: green;
}

.incorrect {
  color: red;
}

.green a::before {
  background: green;
}

.red a::before {
  background: red;
}